var fetchManifest = function fetchManifest(name, host) {
  // console.info('fetchManifest')
  return fetch("".concat(host, "/asset-manifest.json")).then(function (res) {
    return res.json();
  }, function () {
    throw new Error('Not loaded asset-manifest.json');
  });
};

var loadScript = function loadScript(host, manifest) {
  return new Promise(function (res, rej) {
    // console.info('loadScript')
    var script = document.createElement('script');
    script.id = manifest.name;
    script.src = "".concat(host).concat(manifest.files['main.js']);

    script.onload = function () {
      return res();
    };

    script.onerror = function () {
      return rej();
    };

    document.head.appendChild(script);
  });
};

var _render = function render(name) {
  // console.info('render')
  var nameFunc = "".concat(name, "Render");
  var cont = document.getElementById(name);

  if (!cont) {
    throw new Error("The \"".concat(name, "\" micro frontend do not have a container."));
  }

  window[nameFunc](cont);
};

var _unmount = function unmount(name) {
  // console.info('unmount')
  var nameFunc = "".concat(name, "Unmount");
  typeof window[nameFunc] === 'function' ? window[nameFunc]() : new Error("The \"".concat(name, "\" micro frontend was not unmounted."));
};

export default (function (name, host) {
  var loaded;
  return {
    bootstrap: function bootstrap() {
      loaded = new Promise(function (res, rej) {
        fetchManifest(name, host).then(function (manifest) {
          return loadScript(host, manifest).then(function () {
            var nameFunc = "".concat(name, "Render");

            if (typeof window[nameFunc] !== 'function') {
              throw new Error("The \"".concat(name, "\" micro frontend was not bootstrapped."));
            }

            res();
          });
        }).catch(function (error) {
          console.log(error);
          rej();
        });
      });
      return loaded;
    },
    render: function render() {
      // console.info('go render');
      if (!loaded) {
        new Error("The \"".concat(name, "\" micro frontend was not mounted before bootstrapping."));
      }

      loaded.then(function () {
        return _render(name);
      }).catch(function (error) {
        console.error(error);
      });
    },
    unmount: function unmount() {
      if (!loaded) {
        new Error("The \"".concat(name, "\" micro frontend was not unmounted before bootstrapping."));
      }

      loaded.then(function () {
        return _unmount(name);
      }); // loaded = null
    }
  };
});